import React from 'react'
import { LegalSection, Page, Seo } from 'gatsby-theme-portfolio-minimal';

export default function CocoaCloneCoding() {
    return (
        <>
            <Seo title="CocoaCloneCoding" useTitleTemplate={true} noIndex={true}  />
            <Page useSplashScreenAnimation>
              <>
              
            <div
    style={{
      display: "flex",
      alignContent: "center",      
      width:'100%',
      justifyContent: "center",
      flexDirection: 'column',
      alignItems: 'center'
      
    }}
  >
    <div>
      <h1>Nomadcoders - cocoa clone coding</h1></div>
    <div>2023-01-13 ~ 2023-01-15</div>
    <div>
      <iframe src="/clonecoding/cocoa/index.html" title="dummy" style={{width:'25vw',height:'800px'}}></iframe>
    </div>
  
  </div>
            </>
            
            </Page>
        </>
    );
}